@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
body{font-family:"Josefin Sans",sans-serif;margin-top:30px}.visually-hidden{position:absolute;white-space:nowrap;width:1px;height:1px;overflow:hidden;border:0;padding:0;clip:rect(0 0 0 0);-webkit-clip-path:inset(50%);clip-path:inset(50%);margin:-1px}.activity-list{display:flex;align-items:center;flex-direction:column;padding:0}.activity-list h2{margin-top:15px;margin-bottom:15px}.activity-item__title{cursor:pointer;-webkit-transform:scale(1);transform:scale(1);transition:-webkit-transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms;transition:transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms;transition:transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms, -webkit-transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms}.activity-item__title:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}.type-list{padding:20px;margin:0;margin-bottom:20px;max-width:220px;border-radius:20px;list-style:none;background-color:#c8ffd6;box-shadow:5px 12px 17px -3px #42445a}.type-list li{margin-bottom:10px;max-width:200px}.content-container{width:65%;min-height:400px;padding:50px;border-radius:30px;background-color:rgba(83,255,132,.6);box-shadow:8px 8px 24px 0px rgba(66,68,90,.7)}.container{margin:0 auto;text-align:center}.disable-margins{margin:0 auto;padding:0}.current-activity{display:flex;justify-content:center;align-items:center;min-height:70px}.lists-container{display:flex;justify-content:space-evenly}.lists-container .activity-list{width:300px;background-color:#87fca8;box-shadow:0px 11px 13px 0px rgba(66,68,90,.5);border-radius:20px;list-style:none;text-align:left}.activity-item{margin-bottom:20px}.controls{display:flex;justify-content:center}.button-3{-webkit-appearance:none;appearance:none;margin:10px;width:120px;background-color:#2ea44f;border:1px solid rgba(27,31,35,.15);border-radius:6px;box-shadow:rgba(27,31,35,.1) 0 1px 0;box-sizing:border-box;color:#fff;cursor:pointer;display:inline-block;font-size:15px;font-weight:600;line-height:20px;padding:6px 16px;position:relative;text-align:center;text-decoration:none;-ms-user-select:none;user-select:none;-webkit-user-select:none;touch-action:manipulation;vertical-align:middle;white-space:nowrap;-webkit-transform:scale(1);transform:scale(1);transition:-webkit-transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms;transition:transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms;transition:transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms, -webkit-transform cubic-bezier(0.165, 0.84, 0.44, 1) 500ms}.button-3:focus:not(:focus-visible):not(.focus-visible){box-shadow:none;outline:none}.button-3:hover{background-color:#2c974b;-webkit-transform:scale(1.2);transform:scale(1.2)}.button-3--negative{background-color:#fc5757}.button-3--negative:hover{-webkit-transform:scale(1.2);transform:scale(1.2);background-color:#db3a3a}.button-3:focus{box-shadow:rgba(46,164,79,.4) 0 0 0 3px;outline:none}.button-3:disabled{background-color:#94d3a2;border-color:rgba(27,31,35,.1);color:rgba(255,255,255,.8);cursor:default}.button-3:active{background-color:#298e46;box-shadow:rgba(20,70,32,.2) 0 1px 0 inset}.button-3--negative:active{background-color:#ff1515}
